body {
  /* background-color: #f4f4f4; */
  background: linear-gradient(#202338, #101023);
  /* background-image: url("./assets/images/Backgrounds/Background.png");
  background-size: cover; */
  color: #555555;

  font-family: worksan-regular;

  font-size: 15px;
  font-weight: normal;

  line-height: 1em;
  margin: 0;
}
.App {
  height: 100vh;
}
.outer {
  padding: 10px;
}

.container {
  width: 80%;
  margin: auto;
  border: 5px #333333 solid;
  padding: 10px;
  border-radius: 25px;
}

.input {
  width: 1;
  color: #777;
  padding: 8;
  margin: 10;
  width: 200;
}

.myform {
  padding: 20px;
}

.myform input[type="text"],
.myform textarea {
  padding: 8px;
  width: 100%;
}

.column {
  padding: 20px;
  float: left;
  width: 50%;
}

.column_aut {
  padding: 20px;
  float: left;
  width: 33%;
}

.Token_message {
  display: inline;
}

.swap {
  padding: 10px;
}

.swap_button {
  float: right;
}

.markdown {
  width: 80%;
  margin: auto;
}
.discordContainer {
  border: 1px solid #ffffff;
  border-radius: 5px;
  padding: 10px 20px;
}
.discordContainer .discordIcon {
  width: 30px;
  margin: 0px 12px;
}
.discordContainer span {
  color: #ffffff;
  padding: 0px 12px;
}

@font-face {
  font-family: worksan-regular;
  src: url(./assets/fonts/WorkSans-Regular.ttf);
}
@font-face {
  font-family: worksan-bold;
  src: url(./assets/fonts/WorkSans-SemiBold.ttf);
}
